export default {
	data() {
		return {
			currentTab: 0,
			tabClick: 0,
			navIndex: '',
			dataList: ['1', '2', '3'],
			status: ['审核中', '待完成', '已完成'],
			notStart: '',
			start: '',
			end: '',
			userMsg: '',
			guan: '',
			isShow: false,
			showOrder: false,   //弹出评价弹窗
			oneOrder: '',
			oneOrderLength: '',
			userName: '',
			userId: '',
			oid: '',
			aimg: '',
			showUp: false,    //弹出对活动或人员 的评价
			pingjia: '',
			tag: [],
			stag: [],
			type: '',
			tagColor: ['#f6b8f2', '#7ac9cc', '#d6c4ff', '#f6b8f2', '#7ac9cc', '#d6c4ff']
		}
	},
	computed: {
		isWidth: function() {
			return this.$store.state.windowWidth
		},
		isHeight: function() {
			return this.$store.state.windowHeight - 51
		},
		isModel: function() {
			return this.$store.state.model;
		},
		isH: function() {
			return this.$store.state.windowHeight
		}
	},
	onReady() {
		uni.loadFontFace({
			family: 'Alibaba PuHuiTi',
			source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
			success: function() {}
		})
		//用户信息
		let openId = uni.getStorageSync('openid');
		let _this = this;
		uni.request({
			url: _this.$store.state.baseurl + '/user/findArmourUserMessage',
			data: {
				openId: openId
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				_this.userMsg = res.data.data
			}
		})
		//审核中
		uni.request({
			url: _this.$store.state.baseurl + '/order/findOrderMessageByState',
			data: {
				openId: openId,
				orderState: 1
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				_this.notStart = res.data.data
			}
		})

	},
	methods: {
		// 请求事件
		isRequest() {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					uni.showToast({
						icon: 'success',
						title: '请求数据成功'
					})
					resolve()
				}, 1000)
			})
		},
		// 导航栏点击
		navClick(index) {
			this.currentTab = index //设置swiper的第几页
			this.tabClick = index //设置导航点击了哪一个
			this.navIndex = index;

			let _this = this;
			let openId = uni.getStorageSync('openid');

			if (index == 1) {
				//进行中
				uni.request({
					url: _this.$store.state.baseurl + '/order/findOrderMessageByState',
					data: {
						openId: openId,
						orderState: 2
					},
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: function(res) {
						_this.start = res.data.data
					}
				})
			} else if (index == 2) {
				//已完成
				uni.request({
					url: _this.$store.state.baseurl + '/order/findOrderMessageByState',
					data: {
						openId: openId,
						orderState: 4
					},
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: function(res) {
						_this.end = res.data.data
					}
				})
			} else if (index == 0) {
				//审核中
				uni.request({
					url: _this.$store.state.baseurl + '/order/findOrderMessageByState',
					data: {
						openId: openId,
						orderState: 1
					},
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: function(res) {
						_this.notStart = res.data.data
					}
				})
			}
		},
		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current //获取索引
			this.navClick(index)
		},
		lower1() {
			uni.showLoading({
				content: '加载中'
			})
			setTimeout(() => {
				uni.hideLoading()
			}, 1000)
		},
		guanList: function() {
			uni.navigateTo({
				url: "/pages/myAttention/myAttention"
			})
		},
		closeGuan: function() {
			this.isShow = false
		},
		upHead: function(e) {
			uni.navigateTo({
				url: '/pages/upHead/index?pic=' + e
			})
		},
		showPing: function(i) {
			this.oneOrder = this.end[i];
			this.oneOrderLength = Math.ceil(this.end[i].list.length / 3)
			this.showOrder = true
		},
		upStaff: function(i, e, n, c) {
			this.userName = '';
			this.userName = n;
			this.showUp = true;
			this.oid = e;
			this.cid = c;
			this.userId = i,
				this.type = 2;
			let cid = c;
			let openId = uni.getStorageSync('openid');
			let _this = this;
			uni.request({
				url: _this.$store.state.baseurl + '/evaluateLabel/getLabelByCid',
				data: {
					openId: openId,
					cid: cid
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					_this.tag = res.data.data;
				}
			})
		},
		upAct: function(i, e, n, c) {
			this.tag = []
			this.userName = '';
			this.showUp = true;
			this.userName = n;
			this.oid = e;
			this.cid = c;
			this.userId = 1;
			this.type = 1;
		},
		upAimg: function() {
			let _this = this;
			uni.chooseImage({
				count: 1,
				success: (chooseImageRes) => {
					let tempFilePaths = chooseImageRes.tempFilePaths;
					_this.aimg = tempFilePaths[0];
				}
			});
		},
		upOne: function() {
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			let _this = this;
			let openId = uni.getStorageSync('openid');
			let data = {};
			data.oid = parseInt(this.oid);
			data.openId = openId;
			data.byErUserId = this.userId;
			data.evaluateText = this.pingjia;
			data.evaluateType = this.type;
			let tag = '';
			let stag = this.stag;
			for (let i = 0; i < stag.length; i++) {
				if (i == 0) {
					tag += String(stag[i])
				} else {
					tag += '-' + String(stag[i])
				}
			}
			data.elIdStr = tag;
			uni.uploadFile({
				url: _this.$store.state.baseurl + '/evaluateRecord/create', //仅为示例，非真实的接口地址
				filePath: _this.aimg,
				name: 'file',
				formData: data,
				success: (res) => {
					uni.hideLoading()
					let obj = JSON.parse(res.data)
					if (obj.data == 1) {
						_this.userName = '';
						_this.showUp = false;
						_this.showOrder = false;
						_this.pingjia = ''
						uni.showToast({
							title: '评价成功',
							icon:'none',
							duration:3000
						})
					}else if(obj.data == 0) {
						uni.showToast({
							title: '您已对该人员进行了评价',
							icon:'none',
							duration:3000
						})
					} else{
						uni.showToast({
							title: '抱歉，评价上传失败',
							icon:'none',
							duration:3000
						})
					}
				},
				fail() {
					uni.showToast({
						title: '抱歉，评价失败',
						icon:'none',
						duration:3000
					})
				}
			});
		},
		getTag: function(tid) {
			if (this.stag.length < 7) {
				this.stag.push(tid)
			}
		},
		showTel: function() {
			uni.makePhoneCall({
				phoneNumber: '18628188261' //仅为示例
			});
		},
		toOrder: function(e) {
			uni.navigateTo({
				url: '/components/bosspages/order?id=' + e
			})
		},
		orderMsg: function(oid) {
			uni.navigateTo({
				url: '/pages/bossOrderMsg/index?oid=' + oid
			})
		},
		// 关闭评价
		hideCommand() {
			this.showOrder = false
		},
		// 关闭对活动或人员的评价
		hideCommandDetail() {
			this.showUp = false
		}
	}
}
